:root {
  --background: #b3b0b0;
  --backgroundTest: #42434a;
  --foreground: #171717;
  --base-clr: #11121a;
  --line-clr: #42434a;
  --hover-clr: #222533;
  --text-clr: #e6e6ef;
  --accent-clr: #5e63ff;
  --secondary-text-clr: #b0b3c1;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--backgroundTest);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
.currentLink {
  color: var(--accent-clr);
}
.notCurrentLink {
  color: var(--text-clr);
}
.centerLinks {
  font-family: Poppins;
  color: white;
  position: fixed;
  top: 6%;
  left: calc(1340px + 2rem);
  transform: translateY(-50%);
  z-index: 50;
  transition: left 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
}
a {
  margin-right: 10px;
}
.centerText {
  margin-left: -5rem;
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Poppins;
}
.headerText {
  font-size: 50px;
  font-weight: bold;
  color: white;
}
.secondaryHeaderText {
  font-size: 20px;
  color: grey;
}
.formInput {
  border: 2px solid black;
  background-color: white;
}
.formButton {
  background-color: grey;
  border-radius: 10px;
  color: black;
  padding: 8px 12px;
  border: 2px solid black;
}
.stackElements {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 10px;
}
.quizHover:hover {
  background-color: var(--hover-clr);
}
.quizPageTextMain {
  font-size: 30px;
  font-weight: bold;
  color: white;
}
.quizPageTextSecondary {
  font-size: 20px;
  color: grey;
}
.create-bar-container {
  position: absolute;
  margin-left: 14rem;
  top: 230px;
  width: 50%;
  border: 2px solid var(--text-clr);
  border-radius: 15px;
  padding: 20px;
  background-color: var(--base-clr);
  display: flex;
  flex-direction: column;
  gap: 15px;
  box-sizing: border-box;
  font-family: Poppins;
  justify-content: center;
  color: var(--accent-clr);
}
.search-bar-container {
  position: relative;
  width: 100%;
  padding: 6px 18px;
  background: white;
  border: 2px solid black;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
}
.search-controls {
  display: flex;
  align-items: center;
  width: 100%;
  height: 2.2rem;
  background: transparent;
  margin-top: -0.5rem;
}
.search-controls input {
  flex: 1;
  border: none;
  padding: 0px 15px;
  outline: none;
  font-size: 1rem;
  color: black;
}
.dropdown-menu {
  position: absolute;
  top: 3.5rem;
  left: 0rem;
  background-color: #ffffff;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 8px;
  padding: 0.6rem;
  z-index: 10;
  width: 10rem;
}
.dropdown-item {
  display: block;
  text-align: center;
  color: rgb(0, 0, 0);
  text-decoration: none;
  padding: 0.2rem;
  cursor: pointer;
}
.dropdown-item:hover {
  background-color: var(--purple);
}
.youtube-bar-container {
  left: 0%;
  margin: auto;
  width: 45%;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 20px;
  padding: 5px 20px;
  background-color: white;
  flex-direction: column;
  transition: height 0.3s ease;
  position: relative;
  width: 100%;
  color: black;
}
.youtube-controls {
  display: flex;
  align-items: center;
  height: 2.5rem;
  background: transparent;
}
.youtube-controls input {
  flex: 1;
  border: 2px solid rgb(0, 0, 0);
  margin-left: 8;
  padding: 6px;
  outline: none;
  font-size: 1rem;
}
.pdf-file-list {
  margin-top: 8px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.pdf-padding-bottom {
  padding-bottom: 8px;
}
.pdf-file-item {
  position: relative;
  height: 6rem;
  width: 6rem;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: visible;
}
.clear-pdf-btn {
  position: absolute;
  top: -10px;
  right: -12px;
  background: rgba(0,0,0,0.5);
  border: none;
  border-radius: 50%;
  color: white;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.2s ease;
  font-size: 14px;
  padding: 0;
}
.clear-pdf-btn:hover {
  background: rgba(123, 123, 123, 0.7);
}
