  height: 1.5rem;
  font-size: 15;
  background-color: white;
  color: black;
}
.input-quiz-option-secondary {
  border-radius: 8px;
  border: 2px solid var(--background);
  width: 35%;
  text-align: center;
  background-color: white;
  color: black;
}
.input-quiz-option-secondary-two {
  border-radius: 8px;
  border: 2px solid var(--background);
  width: 20%;
  text-align: center;
  margin: 0px 10px;
  height: 1.5rem;
  background-color: white;
  color: black;
}
.main-text {
  margin-left: 15rem;
  font-size: 25px;
}
.youtube-Bar {
  border-radius: 8px;
}
.layout {
  display: flex;
  min-height: calc(100vh - 60px);
}
.main-content {
  flex: 1;
  padding: 20px;
}
